<template>
	<div id="member-phonic">
		<public-header title="语音设置"></public-header>
		<div class="content">
			<div class="padding-10-tb padding-15-lr font-14 c-gray">新语音提示</div>
			<van-cell-group>
				<van-cell title="语音提示">
					<iswitch :value="phonic.voice" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="phonic.voice"></iswitch>
				</van-cell>
				<van-cell title="新消息振动">
					<iswitch :value="phonic.vibrate" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="phonic.vibrate"></iswitch>
				</van-cell>
			</van-cell-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">播放频率</div>
			<van-cell-group>
				<van-cell title="接单">
					<div class="flex" slot="right-icon" @click="onToggleTimes('new')">
						<span class="c-gray" v-if="phonic.new.times == 'loop'">循环播放</span>
						<span class="c-gray" v-else-if="phonic.new.times == 'three'">播放3次</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="退单">
					<div class="flex" slot="right-icon" @click="onToggleTimes('refund')">
						<span class="c-gray" v-if="phonic.refund.times == 'loop'">循环播放</span>
						<span class="c-gray" v-else-if="phonic.refund.times == 'three'">播放3次</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="催单">
					<div class="flex" slot="right-icon" @click="onToggleTimes('remind')">
						<span class="c-gray" v-if="phonic.remind.times == 'loop'">循环播放</span>
						<span class="c-gray" v-else-if="phonic.remind.times == 'three'">播放3次</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">铃声</div>
			<van-radio-group v-model="phonic.voice_type">
				<van-cell-group>
					<van-cell title="音乐" clickable @click="phonic.voice_type = 2">
						<van-radio :name="2" />
					</van-cell>
					<van-cell title="订单内容" clickable @click="phonic.voice_type = 1">
						<van-radio :name="1" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>

			<div class="padding-15">
				<van-button :disabled="!islegal" size="normal" block class="font-16 bg-info" @click="onSubmit">保存</van-button>
			</div>
		</div>
		<van-popup v-model="timesPopup" position="bottom">
			<div class="popup-content">
				<van-picker :columns="times" @cancel="onToggleTimes" @confirm="onConfirmTimes" show-toolbar title="播放次数"/>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import cloudApp from "@/config/cloudapp.js"
	export default {
		components: {
			publicHeader,
			iswitch
		},
		data() {
			return {
				phonic: {
					voice: 1,
					vibrate: 1,
					voice_type: 1,
					new: {
						times: 'loop',
					},
					refund: {
						times: 'loop',
					},
					remind: {
						times: 'loop',
					}
				},
				timesPopup: false,
				times: ['循环播放', '播放3遍'],
				keySelected: '',
				islegal: false,
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				let phonic = cloudApp.igetPrefs('phonic');
				this.phonic = Object.assign(this.phonic, phonic);
				this.islegal = true;
				this.showPreLoading = false;
			},
			onSubmit() {
				if(!this.islegal) {
					return false;
				}
				this.islegal = false;
				cloudApp.isetPrefs('phonic', this.phonic);
				this.util.$toast('设置成功', this.util.getUrl({path: 'pages/shop/setting'}), 1000, 'replace');
			},
			onToggleTimes(key) {
				if(key) {
					this.keySelected = key;
				}
				this.timesPopup = !this.timesPopup;
			},
			onConfirmTimes(value, index) {
				//index == 0循环播放，index == 1,播放3遍
				let times = '';
				if(index == 0) {
					times = 'loop';
				} else {
					times = 'three';
				}
				this.phonic[this.keySelected].times = times;
				this.onToggleTimes();
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#member-phonic .iswitch{
		height: 24px;
	}
	#member-phonic .van-picker__cancel{
		color: #7a7a7a!important;
	}
	#member-phonic .van-picker__confirm{
		color: #33aafc;
	}
</style>


